<template>
  <div class="home">
    <div class="wrapper fadeInDown">
      <div id="formContent">
        <!-- Tabs Titles -->

        <!-- Icon -->
        <div class="fadeIn first">
          <img src="@/assets/logo.png" id="icon" alt="User Icon" />
        </div>

        <!-- Test -->
        <div>
          <h3>For test</h3>
          <span>Usuario: admin@email.com</span><br />
          <span>Password: root</span>
        </div>

        <!-- Login Form -->
        <form v-on:submit.prevent="login">
          <input
            type="text"
            id="login"
            class="fadeIn second"
            name="login"
            placeholder="Usuario"
            v-model="usuario"
          />
          <input
            type="text"
            id="password"
            class="fadeIn third"
            name="login"
            placeholder="Password"
            v-model="password"
          />
          <input type="submit" class="fadeIn fourth" value="Log In" />
        </form>

