<script>
export default {
  name: "Home",
  components: {},
  data: function() {
    return {
      usuario: "",
      password: "",
      error: false,
      error_msg: "",
    };
  },
  methods: {
    login() {
      let json = {
        usuario: this.usuario,
        password: this.password,
      };

      if (json.password == "root" && json.usuario == "admin@email.com") {
        this.$router.push("dashboard");
      } else {
        this.error = true;
        this.error_msg = "Error";
      }
    },
  },
};
</script>

